import { state } from '../../taskState';

class Filter {
  private form: HTMLFormElement;

  constructor() {
    this.form = document.createElement('form');
    this.form.className = 'filter-form';

    this.createTextInput();
    this.createDateInput();
    this.createCheckbox();
    this.createSubmitButton();

    this.form.addEventListener('submit', this.handleSubmit.bind(this));
  }

  private createTextInput() {
    const input = document.createElement('input');
    input.type = 'text';
    input.name = 'search';
    input.placeholder = 'Search...';
    this.form.appendChild(input);
  }

  private createDateInput() {
    const input = document.createElement('input');
    input.type = 'date';
    input.name = 'date';
    this.form.appendChild(input);
  }

  private createCheckbox() {
    const label = document.createElement('label');
    const checkbox = document.createElement('input');
    checkbox.type = 'checkbox';
    checkbox.name = 'showCompleted';
    label.appendChild(checkbox);
    label.appendChild(document.createTextNode(' Show Completed'));
    this.form.appendChild(label);
  }

  private createSubmitButton() {
    const submitButton = document.createElement('button');
    submitButton.type = 'submit';
    submitButton.innerHTML = 'Filter';
    this.form.appendChild(submitButton);
  }

  private handleSubmit(event: Event) {
    event.preventDefault();
    const formData = new FormData(this.form);
    const search = formData.get('search')?.toString().toLowerCase() || '';
    const date = formData.get('date')?.toString() || '';
    const showCompleted = formData.get('showCompleted') === 'on';

   
    const filteredTasks = state.getTasks().filter(task => {
      const matchesSearch = task.title.toLowerCase().includes(search) || task.description.toLowerCase().includes(search);
      const matchesDate = !date || task.date === date;
      const matchesCompletion = showCompleted || !task.completed;

      return matchesSearch && matchesDate && matchesCompletion;
    });

   
  }

  public render(container: HTMLElement) {
    container.appendChild(this.form);
  }
}

export default Filter;
