  box-sizing: border-box;
  outline: none;
  margin: 0;
  padding: 0; }

#container {
  background-color: #edfcf6; }

.main-title {
  text-align: center;
  color: #084c3f; }

.form-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  width: 50vw;
  padding: 20px;
  margin: auto;
  background-color: #08745d;
  color: #edfcf6;
  border-radius: 8px; }

.form {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 1rem; }

.task-body {
  width: 30vw;
  resize: none;
  padding: 10px; }

.form-title,
.select-list,
.date-input,
.task-body,
.add-btn {
  padding: 5px;
  border: none;
  border-radius: 5px;
  cursor: pointer; }
